p, ul, a {
  font-size: 1.2rem;
  font-family: 'Roboto', sans-serif;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h1, h2, h3 {
  font-family: 'Cinzel', serif;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  color: deepskyblue;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  max-width: 1400px;
}

.header-wrapper {
  width: 100%;
}

.header {
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  max-width: 1400px;
  top: 0;
}

.about {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.about .section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 10em 0 10em 0;
}

.about .picture img {
  margin: 5px;
}

.about .text {
  width: 50%;
  max-width: 800px;
}

.about .text ul {
  list-style: circle;
}

.about .text li {
  margin: 1em 0 1em 0;
}

.project {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 1em 0 9em 0;
}

.project .picture {
  overflow: hidden;
  -ms-flex-pack: center;
      justify-content: center;
}

.project .picture img {
  max-width: 100%;
}

#pimp {
  -ms-flex-direction: row;
      flex-direction: row;
}

#pimp .text {
  max-width: 50%;
}

#pimp .picture {
  max-width: 50%;
}

#pimp .picture img {
  -ms-flex-item-align: center;
      align-self: center;
}

#footnote {
  font-size: 0.8em;
  list-style: none;
  padding-left: 0;
}

@media screen and (max-width: 1200px) {


  #root {
    max-width: 100%;
  }

  .about .section {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .about .picture {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    width: auto;
    height: auto;
  }

  .about .picture img {
    height: auto;
    max-width: 100%
  }

  .about .text {
    width: 100%;
    max-width: 100%;
    text-align: center;
  }

  .about .text ul {
    margin: 0;
  }
}

@media screen and (max-width: 800px) {
  .header {
    display: none;
  }
}


